    margin: 5%;
}
:root {
    --mainColor: #0fff95;
  }
  
 #name{
    font-family: 'PT Sans Caption', sans-serif;
    margin: auto;
    margin-top: 100px;
    transition: width 2s;
}
#nameTitle {
    font-size: 72px;
    background:
    linear-gradient(
        to bottom, #0fff95 0%,
        #0fff95 100%
      );
    background:
    linear-gradient(
        to bottom, var(--mainColor) 0%,
        var(--mainColor) 100%
      );
   background-position: 0 100%;
   background-repeat: repeat-x;
   background-size: 4px 4px;
 color: #fff;
 text-decoration: none;
 transition: background-size 1s, color .5s;
 
}
#nameTitle:hover{

    background-size: 5px 77px;
    padding: 1px;
 color: #231f20;
    
}
