.topnav  {
    overflow: hidden;
    color: rgb(8, 184, 101);
    position: static;
    top: 0;
  }
  
  .topnav #myLinks {
    display: none;
    right: 0;
  }
  
  .topnav a {
    color: white;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
    display: block;
  }
  
  .topnav a.icon {
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    width: 100px;
    max-height: 100px;
    margin-right: 50px;
  }
  a.icon i:hover {
    color: #0fff95;
  }
  .topnav a:hover {

    color: #0fff95;
  }
  

